.weather-card {
  border: 1px salmon solid;
  border-radius: 0.5em;
  padding: 1em;
  box-shadow: 5px 5px 5px olive;
  min-width: 10vw;
  margin: 0.5vmin;
}
.weather-card p {
  font-size: small;
}

@media screen and (max-width: 1022px) {
  .weather-card {
    display: inline-block;
  }
}
